import Vue from 'vue'
import Router from 'vue-router'

/* import definition list of vue apps -start- */
import WeatherCard from "../components/WeatherCard.vue";
import PacteraTestsNavi from "../components/PacteraTestsNavi";
/* import definition list of vue apps - end - */

Vue.use(Router)

/* TODO:Not find a better way to config the routed vue apps */


/* Router definition */
const router = new Router({
    routes:[
        {
            path:"/",
            component: PacteraTestsNavi
        }
        ,{
            path:"/weather",
            component: WeatherCard
        }
    ]
})

export default router;

